<template>
  <div class="pay">
    <van-nav-bar fixed title="订单结算台" left-arrow @click-left="$router.go(-1)" />

    <!-- 地址相关 -->
    <div class="address" @click="$router.push('/myAddress')">

      <div class="left-icon">
        <van-icon name="logistics" />
      </div>

      <div class="info" v-if="addressList.address_id">
        <div class="info-content">
          <span class="name">{{ selectAddress.name}} -  </span>
          <span></span>
          <span class="mobile">{{ selectAddress.phone}} </span>
        </div>
        <div class="info-address">
          {{ longAddress }} {{ selectAddress.detail }}
        </div>
      </div>
        <div class="empty-address" v-else>
        <div class="tips">
          您还没有设置收货地址哦，快去设置吧
        </div>
        <div class="btn" @click="$router.push('/newAddress')">添加地址</div>

      </div>

      <!-- <div class="info" v-else>
        请选择配送地址
      </div> -->

      <div class="right-icon">
        <van-icon name="arrow" />
      </div>
    </div>

    <!-- 订单明细 -->
    <div class="pay-list">
      <div class="list">
        <div v-for="item in shopList" class="goods-item" :key="item.goods_id">
          <div class="left">
            <img :src="item.goods_image" alt="" />
          </div>
          <div class="right">
            <p class="tit text-ellipsis-2">
              {{ item.goods_name }}
            </p>
            <p class="info">
              <span class="count">x{{ item.total_num }}</span>
              <span class="price">¥{{ item.goods_price_min }}</span>
            </p>
          </div>
        </div>
      </div>

      <div class="flow-num-box">
        <span>共 {{ total.orderTotalNum }} 件商品，合计：</span>
        <span class="money">￥{{ total.orderTotalPrice }}</span>
      </div>

      <div class="pay-detail">
        <div class="pay-cell">
          <span>订单总金额：</span>
          <span class="red">￥{{ total.orderTotalPrice }}</span>
        </div>

        <div class="pay-cell">
          <span>优惠券：</span>
          <span>无优惠券可用</span>
        </div>

        <div class="pay-cell">
          <span>配送费用：</span>
          <span v-if="false">请先选择配送地址</span>
          <span v-else class="red">+￥0.00</span>
        </div>
      </div>

      <!-- 支付方式 -->
      <div class="pay-way">
        <span class="tit">支付方式</span>
        <div class="pay-cell">
          <span><van-icon name="balance-o" />余额支付（可用 ¥ {{ person }} 元）</span>
          <!-- <span>请先选择配送地址</span> -->
          <span class="red"><van-icon name="passed" /></span>
        </div>
      </div>

      <!-- 买家留言 -->
      <div class="buytips">
        <textarea placeholder="选填：买家留言（50字内）" v-model="remark" name="" id="" cols="30" rows="10"></textarea>
      </div>
    </div>

    <!-- 底部提交 -->
    <div class="footer-fixed">
      <div class="left">实付款：<span>￥{{ total.orderTotalPrice }}</span></div>
      <div class="tipsbtn" @click="buyOrder">提交订单</div>
    </div>
  </div>
</template>

<script>
import { getAddressApi, getDefaultIdApi } from '@/api/address'
// import cart from '@/store/modules/cart'
import { getOrderApi, buyOrderApi } from '@/api/order'
import { Toast } from 'vant'
// import { mapGetters } from 'vuex'
export default {
  name: 'PayIndex',
  data () {
    return {
      remark: '请留言',
      addressList: {}, // 地址
      shopList: [], // 商品列表
      total: {}, // 总价与总数
      person: {} // 个人余额
    }
  },
  created () {
    this.getAddressList() // 渲染获取收货地址列表
    this.getOrderList() // 渲染获取订单页面
  },
  computed: {
    // 详细地址
    longAddress () {
      const region = this.selectAddress.region
      return Object.values(region || {}).join(' ')
    },

    // 选中的地址
    selectAddress () {
      return this.addressList
    },

    // 获取请求过来的mode与cartIds
    mode () {
      return this.$route.query.mode
    },
    cartIds () {
      return this.$route.query.cartIds
    },
    goodsId () {
      return this.$route.query.goodsId
    },
    goodsNum () {
      return this.$route.query.goodsNum
    },
    goodsSkuId () {
      return this.$route.query.goodsSkuId
    }
  },
  methods: {
    // 封装获取收货地址列表
    async getAddressList () {
      const { data: { list } } = await getAddressApi()
      // 获取默认收货地址Id
      const { data: { defaultId } } = await getDefaultIdApi()
      // 找出对应的默认收货地址
      const defaultAds = list.find(item => item.address_id === defaultId)
      console.log(defaultAds)
      this.addressList = defaultAds || {}
    },

    // 封装获取商品订单
    async getOrderList () {
      // 判断是购物车模式还是直接购买模式
      if (this.mode === 'cart') {
        const res = await getOrderApi(this.mode, {
          cartIds: this.cartIds
        })
        const { data } = res
        const { data: { order: { orderTotalNum, orderTotalPrice }, personal } } = res
        this.shopList = data.order.goodsList // 商品列表
        this.total = { orderTotalNum, orderTotalPrice }
        this.person = personal.balance
      } else {
        const res = await getOrderApi(this.mode, {
          goodsId: this.goodsId,
          goodsNum: this.goodsNum,
          goodsSkuId: this.goodsSkuId
        })
        const { data } = res
        const { data: { order: { orderTotalNum, orderTotalPrice }, personal } } = res
        this.shopList = data.order.goodsList // 商品列表
        this.total = { orderTotalNum, orderTotalPrice }
        this.person = personal.balance
      }
    },

    // 封装提交订单
    async buyOrder () {
      if (!this.addressList.address_id) {
        Toast.fail('请选择收货地址')
        return
      }
      if (this.mode === 'cart') {
        const res = await buyOrderApi(this.mode, {
          cartIds: this.cartIds,
          remark: this.remark
        })
        console.log(res)
      } else {
        const res = await buyOrderApi(this.mode, {
          goodsId: this.goodsId,
          goodsNum: this.goodsNum,
          goodsSkuId: this.goodsSkuId,
          remark: this.remark
        })
        console.log(res)
      }
      this.$toast.allowMultiple()
      this.$toast.loading({
        message: '加载中...',
        forbidClick: true,
        duration: 500
      })
      await this.$sleep(500)
      this.$toast.success('支付成功')
      await this.$sleep(1000)
      this.$router.replace('/myorder')
    }
  }
}

</script>

<style lang="less" scoped>
.empty-address {
  .tips {
    text-align: center;
    color: #666;
    margin: 30px;
  }
  .btn {
    width: 110px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    background-color: #fa2c20;
    border-radius: 16px;
    color: #fff;
    display: block;
    margin: 0 auto;
  }
}
.pay {
  padding-top: 46px;
  padding-bottom: 46px;

  ::v-deep {
    .van-nav-bar__arrow {
      color: #333;
    }
  }
}

.address {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 20px;
  font-size: 14px;
  color: #666;
  position: relative;
  background: url(@/assets/border-line.png) bottom repeat-x;
  background-size: 60px auto;

  .left-icon {
    margin-right: 20px;
  }

  .right-icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-7px);
  }
}

.goods-item {
  height: 100px;
  margin-bottom: 6px;
  padding: 10px;
  background-color: #fff;
  display: flex;

  .left {
    width: 100px;

    img {
      display: block;
      width: 80px;
      margin: 10px auto;
    }
  }

  .right {
    flex: 1;
    font-size: 14px;
    line-height: 1.3;
    padding: 10px;
    padding-right: 0px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    color: #333;

    .info {
      margin-top: 5px;
      display: flex;
      justify-content: space-between;

      .price {
        color: #fa2209;
      }
    }
  }
}

.flow-num-box {
  display: flex;
  justify-content: flex-end;
  padding: 10px 10px;
  font-size: 14px;
  border-bottom: 1px solid #efefef;

  .money {
    color: #fa2209;
  }
}

.pay-cell {
  font-size: 14px;
  padding: 10px 12px;
  color: #333;
  display: flex;
  justify-content: space-between;

  .red {
    color: #fa2209;
  }
}

.pay-detail {
  border-bottom: 1px solid #efefef;
}

.pay-way {
  font-size: 14px;
  padding: 10px 12px;
  border-bottom: 1px solid #efefef;
  color: #333;

  .tit {
    line-height: 30px;
  }

  .pay-cell {
    padding: 10px 0;
  }

  .van-icon {
    font-size: 20px;
    margin-right: 5px;
  }
}

.buytips {
  display: block;

  textarea {
    display: block;
    width: 100%;
    border: none;
    font-size: 14px;
    padding: 12px;
    height: 100px;
  }
}

.footer-fixed {
  position: fixed;
  background-color: #fff;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  border-top: 1px solid #efefef;
  font-size: 14px;
  display: flex;

  .left {
    flex: 1;
    padding-left: 12px;
    color: #666;

    span {
      color: #fa2209;
    }
  }

  .tipsbtn {
    width: 121px;
    background: linear-gradient(90deg, #f9211c, #ff6335);
    color: #fff;
    text-align: center;
    line-height: 46px;
    display: block;
    font-size: 14px;
  }
}</style>
