import request from '@/utils/request'

// 获取收货地址列表
export const getAddressApi = () => {
  return request.get('/address/list')
}
// 获取收货地址详情
export const getAddressDetailApi = (addressId) => {
  return request.get('/address/detail', {
    params: {
      addressId
    }
  })
}
// 添加收货地址
export const addAddressApi = (name, phone, region, detail) => {
  return request.post('/address/add', {
    form: {
      name, // 收货人姓名
      phone, // 收货人电话
      region, // 数组[ {"value":107,";abel":"上海"}]
      detail
    }
  })
}

// 修改收货地址
export const editAddressApi = (addressId, name, phone, region, detail) => {
  return request.post('/address/edit', {
    addressId,
    form: {
      name, // 收货人姓名
      phone, // 收货人电话
      region, // 数组[ {"value":107,";abel":"上海"}]
      detail
    }
  })
}
// 删除收货地址
export const delAddressApi = (addressId) => {
  return request.post('/address/remove', {
    addressId
  })
}

// 获取省市区
export const getRegionsApi = () => {
  return request.get('/region/tree')
}

// 设置默认地址ID
export const setDefaultIdApi = (addressId) => {
  return request.post('/address/setDefault', { addressId })
}

// 获取默认地址ID
export const getDefaultIdApi = () => {
  return request.get('/address/defaultId')
}
